.content_container {
    height: calc(100vh - 64px - 80px);
    padding-left: 45px;
    padding-top: 80px;
}

#student_img_container {
    float: right;
    width: 50%;
    margin-top: -5vh;
}

#student_img_container > img {
    width: 100%;
    height: auto;
}

.first_header, .second_header {
