    font-family: 'Inter', sans-serif;
}

.first_header {
    width: 65vw;
}
.second_header {
    margin-top: 15vh;
    margin-bottom: 5vh;
    width: 40vw;
}

.button:first-of-type {
    margin-right: 1vw;
}

